import React from "react";
import ReactDOM from "react-dom/client";
import { Emulator } from "android-emulator-webrtc/emulator";

// Optionally, import your own CSS or additional assets

// Create a root using ReactDOM.createRoot (React 18+ API)
const container = document.getElementById("root");
const root = ReactDOM.createRoot(container);

// Render the Emulator component with your desired URI
root.render(
    <React.StrictMode>
        <Emulator uri="https://canlite.org" />
    </React.StrictMode>
);
